<!-- 系统首页 -->
<template>
    <div class="content-header">
        <span class="title">关于我们</span>
        <span class="subtitle">Welcome to the Growth assessment system</span>
    </div>
    <div class="content-body">
        <div class="grid">
            <div class="grid-item" v-for="n in 9" :key="n">
                <span>{{ cultureNineLetter[n - 1] }}</span><br>
                <span>{{ cultureNineText[n - 1] }}</span>
            </div>
        </div>
    </div>
    <div class="content-footer">
        <a class="btn" @click="handleClick">进入系统
            <span>
                <ArrowRightOutlined class="arrow-icon" />
            </span>
        </a>
    </div>
</template>

<script setup>
import { ref } from "vue"
import { useRouter } from 'vue-router'
import { ArrowRightOutlined } from '@ant-design/icons-vue'
import { message } from 'ant-design-vue'

const router = useRouter()
// 从config中获取userRole信息
const userRole = ref(localStorage.getItem('userRole'))
const username = ref(localStorage.getItem('username'))
const handleClick = () => {
    if(localStorage.getItem('isFirstLogin') === 'true'){
        if (userRole.value === '0') {
            router.push({ path: '/personalInfo' })
            message.success({
                content: '哈喽！' + username.value + '，首次登录请修改密码哦~',
                style: { marginTop: '5vh' },
                duration: 5,  // 设置显示时间为 5 秒
            });
        } else {
            router.push({ path: '/personalInfoAd' })
            message.success({
                content: '哈喽！' + username.value + '，首次登录请修改密码哦~',
                style: { marginTop: '5vh' },
                duration: 5,  // 设置显示时间为 5 秒
            });
        }
    }else {
        if (userRole.value === '0') {
            router.push({ path: '/ordinary' })
            message.success({
                content: '哈喽！' + username.value + '，欢迎来到成长评估系统，我会陪你一起进步~',
                style: { marginTop: '5vh' },
            });
        } else {
            router.push({ path: '/admin' })
            message.success({
                content: '哈喽！' + username.value + '，欢迎来到成长评估系统，我会陪你一起进步~',
                style: { marginTop: '5vh' },
            });
        }
    }
}

const cultureNineLetter = ref(['O', 'W', 'I', 'H', 'R', 'D', 'M', 'F', 'I'])
const cultureNineText = ref(['思想上开放进取', '作风上把打胜仗作为信仰', '业务上创新求实', '工作中信任互助', '员工间尊重平等', '行业上业精于勤', '执行力使命必达', '团队上五湖四海', '成长上坚持自省'])
</script>

